<template>
  <li class="potentialListItem pr">
    <img
      src="/img/alarmItem.png" alt="alarm ico"
      class="alarmClockImg"
      v-if="item.currentPrice<=item.desiredPrice"
    />
    <div class="exchangeBox">
      <a :href="makeUrl(item.exchange,item.coin)" target="_blank">
        <img :src="'/img/'+item.exchange+'.jpg'" style='width:100%' :alt="item.exchange"/>
      </a>
    </div>
    <div class="symbolBox">
      <img :src="item.coinUrl" style='width:100%' alt="coin ico"/>
    </div>
    <div class="coinBox">
      <div class="name hideMob" >{{item.name}}</div>
      <div>{{item.coin.toUpperCase()}}</div>
    </div>
    <div class="percBox" :class="{'green':item.priceChangePercentage24h>0,'red':item.priceChangePercentage24h<0}">
      <span class="grey label hideMob">24h%: </span>
      {{item.priceChangePercentage24h| numFormat('% 0,0')}}
    </div>
    <div class="desPriceBox">
      <span class="grey label">desPrice: </span>
      <input
        type="text"
        v-model.number="item.desiredPrice"
        class="dPriceBox desInput"
        v-on:keyup.enter="(ev) => {ev.target.blur();update()}"
        style="display: inline-block"
      >
<!--      {{item.desiredPrice | numberPrecision(5)}}-->
    </div>
    <div class="cPriceBox curr">
      <span class="grey label hideMob">cPrice: </span>
      {{item.currentPrice}}
    </div>
    <div>
      <span class="grey label">dumPrice: </span>
      <input
        type="text"
        v-model.number="item.dumpPrice"
        class="dPriceBox dumpInput"
        v-on:keyup.enter="(ev) => {ev.target.blur();update()}"
        style="display: inline-block"
      >
    </div>
    <div class="xsBox grey"><span class="label">=</span> {{(item.currentPrice/item.dumpPrice) | numFormat('0,0.0')}}<span class="label">x</span></div>
    <!--    ath price-->
    <div>
      <span class="grey hideDesk label">ATHPrice: </span>
      <input
        type="text"
        v-model.number="item.athPrice"
        class="dPriceBox dumpInput"
        v-on:keyup.enter="(ev) => {ev.target.blur();update()}"
      >
      <span class="grey label"> =</span>
    </div>
    <!--    axs-->
    <!--    <div class="xsBox grey">-->
    <!--      {{(item.currentPrice/(item.athPrice)) | numFormat('0,0.00')}}-->
    <!--      <span class="grey label">x</span>-->
    <div class="xsBox grey">
      {{((-item.athPrice/(item.currentPrice))+1) | numFormat('% 0,0')}}
      <!--      <span class="grey label">%</span>-->
    </div>
    <!--    ssssss-->
    <div class="mcBox">
      <span class="grey label">MC#: </span><span class="hideMob">#</span>{{item.marketCapRank | numFormat('0,0')}}
    </div>
    <div class="dateBox grey" @click="changeExchange(item.coin)">
      <span class="grey label">date: </span>
      {{item.date}}
    </div>

    <div class="graphBox hideMob">
      <a :href="`https://www.coingecko.com/en/coins/${item.id}`" target="_blank">
        <trend
          :data="item.sparklineIn7d"
          :gradient="item.priceChangePercentage24h<0 && item.sparklineIn7d?['#f6465d']:['#42d69c']"
          auto-draw
          :autoDrawDuration="700"
          :stroke-width="2"
          smooth
        />
      </a>
    </div>
    <textarea type="text" v-model="item.comment" class="commentBox comment" v-on:keyup.enter="(ev) => {ev.target.blur();update()}"/>
    <button @click="deleteItem(item.id)" class="delBox">x</button>
  </li>
</template>

<script>
  import SubTitle from '../SubTitle';
  import TradeForm from '../TradeForm';
  import PotentialForm from './PotentialForm';
  import numberPrecision from '../../helpers/numberPrecision';
  import makeUrlFromExchangeName from '../../helpers/makeUrlFromExchangeName';
  export default {
    props:['item'],
    components: {
      PotentialForm,
      TradeForm,
      SubTitle
    },
    methods: {
      makeUrl(ex,coin){
        return makeUrlFromExchangeName(ex,coin)
      },
      update(){
        this.$store.dispatch('updateFB',{name:'potentialList',info:this.$store.state.isAK?'infoAK':'infoMain'});
      },
      deleteItem(id){
        this.$store.commit('deleteItem', {id,listName:'potentialList'})
      },
      changeExchange(coin){
        const coinObj = this.$store.state.infoMain.potentialList.find(item=>item.coin===coin)
        const ex = prompt('заменить биржу на', '')
        if (ex) {
          coinObj.exchange = ex
          coinObj.exchangeUrl = '/img/'+ex+'.jpg'
          this.$store.dispatch('updateFB',{name:'potentialList',info:this.$store.state.isAK?'infoAK':'infoMain'});

        }
      },
    },
    filters: {// html -> {{product.price | numberReadableFormat}} ₽ - значение слева передается аргументом в правую функцию. Фича Vue. Либо аналогом computed. А если просто html {{numberReadableFormat(value)}} то б ошибка на нереактивность.
      numberPrecision,
    },
  };
</script>

