<script>

  export default {
    props:['inside', 'exchange', 'coin'],
    components: {},
    data() {//локальн стейт
      return {
        formState: {
          exchange:'Binance',
          coin: '',
          desiredPrice: '',
          manualId: '',
        },
      };
    },
    computed: {
      total() {
        return this.form.price * this.form.amount
      },
      direction(){
        if (this.radioDirection){
          return -1
        }else{
          return 1
        }
      }
    },
    methods: {
      addPotentialAlt(){
        const potentialItem ={
          exchange:this.formState.exchange,
          desiredPrice:this.formState.desiredPrice,
          manualId: this.formState.manualId,
          coin:this.formState.coin.toLowerCase(),
          date:new Date().toLocaleString('en-US', { year: 'numeric', month: '2-digit', day: '2-digit' })
        }
        this.$store.dispatch('loadPotentialAlt',{
          potentialItem,
          info:this.$store.state.isAK?'infoAK':'infoMain',
          listName:'potentialList'
        })
        this.resetForm();
      },
      resetForm(){
        this.formState.exchange = 'Binance';
        this.formState.coin = '';
        this.formState.desiredPrice = '';
        this.formState.manualId = '';
      }
    },
  }
