<template>
  <section>
    <SubTitle title="Potentials to watch"/>
    <ul class="card p20 pr">
      <FilterBlock items-name="letters" :selected-item.sync="selectedLetter"/>
      <FilterBlock items-name="exchanges" :list="this.$store.state[this.$store.state.isAK?'infoAK':'infoMain'].potentialList||[]" :selected-item.sync="selectedExchange"/>
      <FilterBlockLabelsPotentials :selected-label.sync="selectedLabel"/>
      <li class="amountCoins hover">{{computedSortedNewList.length}} шт</li>
      <PotentialListItem
        v-for="item in computedSortedNewList"
        :key="item.id"
        :item="item"
      />
      <PotentialForm style="margin-top: 50px;"/>
    </ul>

  </section>
</template>

<script>
  import SubTitle from '../../components/SubTitle';
  import FilterBlock from '../../components/AltsList/FilterBlock';
  import PotentialListAlphabet from '../../components/potential/PotentialListAlphabet';
  import PotentialListItem from '../../components/potential/PotentialListItem';
  import PotentialForm from '../../components/potential/PotentialForm';
  import TradeForm from '../../components/TradeForm';
  import FilterBlockLabels from '../../components/AltsList/FilterBlockLabels';
  import FilterBlockLabelsPotentials from '../../components/AltsList/FilterBlockLabelsPotentials';

  export default {
    props:['list'],
    components: {
      FilterBlockLabelsPotentials,
      FilterBlock,
      PotentialListAlphabet,
      PotentialListItem,
      PotentialForm,
      TradeForm,
      SubTitle
    },
    data(){
      return{
        selectedLetter:'all',
        selectedExchange:'all',
        selectedLabel:'priceChangePercentage24h',
      }
    },
    computed:{
      computedSortedNewList(){
        let newList = this.list
        if(!this.list){
          newList = this.$store.state[this.$store.state.isAK?'infoAK':'infoMain'].potentialList;
        }

        //сначала сортировка по лейблам
        const label = this.selectedLabel// сразу this.selectedLabel не получается поэтому const
        newList = newList.sort(function (a, b) {
          if (a[label] > b[label]) {
            return label==='coin'||label==='marketCapRank'?1:-1;//если алфавит то с по возрастанию, остальные по увыванию
          }
          if (a[label] < b[label]) {
            return label==='coin'||label==='marketCapRank'?-1:1;
          }
          return 0;
        });

        //потом фильтрация по буквам
        if (this.selectedLetter!=='all'){
          newList = newList.filter(item=>item.coin[0]===this.selectedLetter.toLowerCase());
        }
        //потом фильтрация по риск категории

        if (this.selectedRiskCategory && this.selectedRiskCategory!=='all'){
          newList = newList.filter(item => item.riskCategory === this.selectedRiskCategory)
        }
        // if (this.selectedRiskCategory==='all w/o trash'){
        //   newList = newList.filter(item => item.riskCategory !== 'trash')
        // }
        //потом фильтрация по инд категории

        if (this.selectedIndustryCategory && this.selectedIndustryCategory!=='all'){
          newList = newList.filter(item => item.industryCategory === this.selectedIndustryCategory)
        }

        //потом фильтрация по алфавиту

        if (this.selectedLetter!=='all'){
          newList = newList.filter(item=>item.coin[0]===this.selectedLetter.toLowerCase());
        }

        //потом фильтрация по биржам

        if (this.selectedExchange!=='all'){
          newList = newList.filter(item => item.exchange===this.selectedExchange)
        }

        return newList
      },
    }

  };
</script>

<style scoped>
  .amountCoins{
    position: absolute;
    right:10px;
    top:0;
    font-size: 14px;
    color: rgba(255, 255, 255, 0.4);
  }

  @media only screen and (max-width: 768px) {
    .amountCoins {
      font-size: 10px;
    }
  }


</style>
